<template>
	<div id="heart-recharge">
		<common-header :title="'爱心充值'"></common-header>
    <Cell :title="'充值方式'" @click="rechargeWay" :value="way"/>
    
    <div class="link-cell no-arrow">
	    <span>金额</span>
	    <input type="number" class="text-input needsclick" placeholder="1块钱兑换10个爱心" v-model="heartNum">
	  </div>
	  
    <div class="btn" :class="{'active':isComplete}" @click="payment">充值</div>
    <div class="text">
    	充值后系统可能会延迟到账，若您长时间未到账，请与我们联系
    </div>
    
    <div class="shadow" v-if="showOrderType">
      <BottomPopup :itemList="itemList" @selItem="getBottomPopup"></BottomPopup>
    </div>
    
    <div class="shadow" v-show="showPaw">
			<div class="psw-wrap">

				<div class="title">
					<img class="lt-arrow" src="../../../assets/shan-right.png" @click="hidden" /> 请输入支付密码
				</div>
				<div class="content">
					<div class="psw-input">
						<input type="password" name="" placeholder="支付密码" v-model="paypsw" /><span class="payment-btn active" @click="getPaySubmit(3)">付款</span>
					</div>
					<!--<div class="forget" @click="forgetPsw">忘记密码？</div>-->
				</div>
			</div>
		</div>
    
	</div>
</template>

<script>
import Cell from '@/components/Cell/Cell';
import BottomPopup from '@/components/Popup/BottomPopup'
import md5 from 'blueimp-md5';

export default {
	data(){
		return {
			way:'选择充值方式',
			heartNum:'',
      showOrderType: false,
			itemList:[
				{text:"微信充值",id:1},
				{text:"钱包充值",id:2}
			],
			wayId:'',
			showPaw: false,
			paypsw: '',
			wxPayObj: '',
			isPaypw:'',
		}
	},
	created(){
		this.getUserDetails()
	},
	computed: {
		isComplete() {
			var isTrue = false;
			if(this.way!='选择充值方式' && this.heartNum > 0) {
				isTrue = true;
			}
			return isTrue;
		}
	},
  components: {
  	Cell,
  	BottomPopup
  },
  methods:{
  	rechargeWay() {
      this.showOrderType = true
    },
    hidden() {
			this.showPaw = false
		},
    getBottomPopup(res){
			if(res != 'cancel'){
				this.way = res.text
				this.wayId = res.id
			}
			this.showOrderType = false
		},
		getUserDetails() {
			this.$Api.UserDetails(0).then((res) => {
				if(res.q.s == 0){
					this.isPaypw = res.q.user.isPaypw
				}
			})
		},
		payment() {
			if(this.wayId == 1) {
				this.getPaySubmit(11)
				//微信支付
			} else if(this.wayId == 2) {
				if(this.isPaypw == 1){
					this.showPaw = true
				}else{
					this.$popup({
						title: '设置密码',
						content: '请先设置支付密码'
					}).then((res) => {
						if(res == 'sure'){
							this.$router.push({name:'EditPay',query:{type:1}})
						}
					})
				}
			}
		},
		
		getPaySubmit(opt) {
				let params = {}
				if(opt == 3) {
					if(this.paypsw.length > 0) {
						params = {
							a: 2,
							payment: opt,
							amount: this.heartNum,
							psw: md5(this.$paykey + md5(this.paypsw))
						}
					} else {
						this.$toast('请输入密码')
						return
					}
				} else if(opt == 11) {
					params = {
						a: 2,
						payment: opt,
						amount: this.heartNum,
					}
				}
				this.$Api.PaySubmit(params).then((res) => {
					console.log(res)
					this.paypsw = ''
					if(res.q.s == 0) {
						if(opt == 3) {
							this.$toast('充值成功')
							setTimeout(()=>{
								this.$router.go(-1)
							},1500)
						} else if(opt == 11) {
							this.wxPayObj = JSON.parse( res.q.wxPay )
							this.onBridgeReady()
						}
					}else {
						this.$toast(res.q.d)
					}
				})
			},
			onBridgeReady() {
			let self = this
			console.log(this.wxPayObj)
			WeixinJSBridge.invoke(
				'getBrandWCPayRequest', {
					"appId": this.wxPayObj.appId, //公众号名称，由商户传入     
					"timeStamp": this.wxPayObj.timeStamp, //时间戳，自1970年以来的秒数     
					"nonceStr": this.wxPayObj.nonceStr, //随机串     
					"package": this.wxPayObj.package,
					"signType": this.wxPayObj.signType, //微信签名方式：     
					"paySign":  this.wxPayObj.paySign//微信签名 
				},
				function(res) {
					console.log(res)
					if(res.err_msg == "get_brand_wcpay_request:ok") {
						self.$toast('充值成功')
						setTimeout(()=>{
							self.$router.replace({name:'LoveHeart'})
						},1500)
					} // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
				}
			);
		},
  },
  
}
</script>

<style lang="scss">
	@import "../../../sass/baseSass.scss";
	#heart-recharge{
		height: 100%;
		background: #f0f0f0;
		.btn{
			font-size: 0.45rem;
			margin: 0.8rem auto 0.67rem;
			width: 9.2rem;
			height: 1.2rem;
			line-height: 1.2rem;
			border-radius: 0.6rem;
			text-align: center;
			background: #ccc;
			color: #fff;
		}
		.active{
			@include bg-color;
		}
		.text{
			padding: 0 0.4rem;
			font-size: 0.37rem;
			color: #666;
		}
		.link-cell {
	    position: relative;
	    display: flex;
	    flex-direction: row;
	    justify-content: space-between;
	    padding: 0 0.8rem 0 0.4rem;
	    height: 1.2rem;
	    line-height: 1.2rem;
	    background: #fff;
	    font-size: 0.427rem;
	    color: #333;
	
	    &:after {
	      content: '';
	      position: absolute;
	      top: 50%;
	      right: 0.4rem;
	      width: 0.173rem;
	      height: 0.307rem;
	      transform: translateY(-50%);
	      background-image: url('../../../assets/arrow_gray.png');
	      background-size: 100%;
	    }
	  }
	  .no-arrow {
	    padding-right: 0.4rem;

	    &:after {
	      content: none;
	    }
	  }
	  .shadow {
			.psw-wrap {
				position: absolute;
				right: 0;
				bottom: 0;
				left: 0;
				background: #fff;
				.title {
					position: relative;
					height: 1.2rem;
					line-height: 1.2rem;
					font-size: 0.48rem;
					color: #000;
					text-align: center;
					border-bottom: 1px solid #ccc;
					.lt-arrow {
						@include lt-arrow;
						width: 0.24rem;
						height: 0.45rem;
					}
				}
				.content {
					padding: 0.79rem 0.32rem 1.4rem 0.32rem;
					overflow: hidden;
				}
				.psw-input {
					border: 1px solid #999999;
					border-radius: 4px;
					height: 1.15rem;
					position: relative;
					>input {
						border: none;
						width: 100%;
						height: 100%;
						padding: 0 0.2rem;
						margin-bottom: 0;
						line-height: 1.15rem;
						font-size: 0.43rem;
					}
					.payment-btn {
						position: absolute;
						right: 0;
						top: 0;
						border-radius: 0 4px 4px 0;
						height: 1.1rem;
						line-height: 1.15rem;
						font-size: 0.43rem;
						width: 1.87rem;
						text-align: center;
						background-color: #d9d9d9;
						color: #999999;
					}
					.payment-btn.active {
						color: #fff;
						@include bg-color;
					}
				}
				.forget {
					font-size: 0.43rem;
					float: right;
					margin-top: 0.4rem;
				}
			}
		}
	}
</style>


